import React from "react"
import { Link } from "gatsby"
import Logo from '../components/Navbar/Logo'
import Navbar from "../components/Navbar/Navbar"

// navbar style
const NavStyle = {
   color: "#232129",
   fontFamily: "-apple-system, Roboto, sans-serif, serif",
}

// styles
const pageStyles = {
   color: "#232129",
   fontFamily: "-apple-system, Roboto, sans-serif, serif",
}

// title content styles
const TitleContentStyles = {
   margin: 'auto',
   width: '95%',
  //  border: '3px solid whitesmoke',
   padding: '5px'
}

// title heading styles
const TitleHeadingStyles = {
   color: 'white',
   textAlign: 'center'
}

const FormLayout = {
   backgroundColor: '#101010',
   border: '3px solid #404040',
   width: '95%',
   borderRadius: '30px',
   margin: 'auto',
   textAlign: 'center'
 }

const InputsLayout = {
   margin: '20px'
}

const Labels = {
   color: 'whitesmoke',
   fontSize: '25px'
}

const Contact = () => {
   return (
      <div>
         <div style={NavStyle}>
            <Navbar/>
         </div>
         <main style={pageStyles}>
            {/* TAB TITLE */}
            <title>CONTACT</title>
            {/* PAGE HEADING */}
            <div style={TitleContentStyles}>
               <h1 style={TitleHeadingStyles}>
                  CONTACT
               </h1>
            </div>
            <div style={FormLayout}>
               <form style={InputsLayout} action="/">
                  <label style={Labels} for="fname">First Name</label>
                  <input style={InputsLayout} type="text" id="fname" name="fname"/><br/>
                  <label style={Labels} for="lname">Last Name</label>
                  <input style={InputsLayout} type="text" id="lname" name="lname"/><br/>
                  <input style={InputsLayout} type="submit" value="Submit"/>
               </form>
            </div>
         </main>
      </div>
   );
}

export default Contact;
